<template>
  <div id="searchUserModal">
    <div class="card">
      <div class="card-header">
        <h2>
          Search user
          <button
            class="btn btn-flat btn-secondary float-right"
            @click="$emit('close')"
          >&times;</button>
        </h2>
      </div>
      <div class="card-body">
        <div class="form-group">
          <label for="searchUsername" class="bmd-label-floating">Username</label>
          <input
            type="text"
            class="form-control"
            id="searchUsername"
            @keyup="setTimer()"
            @keydown="clearTimer()"
            v-model="username"
            autocomplete="off"
          >
          <span class="bmd-help">Search for username</span>
        </div>
      </div>
      <div class="card-footer" style="max-height: 400px; overflow-y: auto">
        <ul class="list-group user-list">
          <a
            href="#"
            class="list-group-item p-0"
            v-for="user in searchedUserList"
            v-bind:key="user._id"
            @click="createConversation(user)"
          >
            <img :src="user.imgURL" class="user-image rounded-circle">
            <div class="bmd-list-group-col">
              <h5 class="list-group-item-heading">{{user.name}}</h5>
              <p class="list-group-item-text m-0">{{user.email}}</p>
            </div>
          </a>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapState } from "vuex";

export default {
  name: "SearchUserModal",
  props: ["userInfo"],
  data() {
    return {
      userList: [],
      userIdList: [],
      timerId: null,
      typingInterval: 500,
      username: "",
      searchedUserList: [],
      typingTimer: null
    };
  },
  computed: {
    ...mapGetters(["getUserList"])
  },
  methods: {
    setTimer: function() {
      clearTimeout(this.typingTimer);
      this.typingTimer = setTimeout(this.searchUsername, this.typingInterval);
    },
    clearTimer: function() {
      clearTimeout(this.typingTimer);
    },
    searchUsername: function() {
      let vm = this;
      if (
        this.username == "" ||
        this.username == undefined ||
        this.username == null
      )
        return;
      this.searchedUserList = this.getUserList.filter(u => {
        return (
          u.name.toLowerCase().indexOf(vm.username.toLowerCase()) > -1 ||
          u.email.indexOf(vm.username.toLowerCase()) > -1
        );
      });
      console.log(this.searchedUserList);
    },
    createConversation: function(u) {
      this.$store.dispatch("CREATE_CONVERSATION", {
        u,
        userInfo: this.userInfo
      });
      this.$store.dispatch("GET_CONVERSATION", this.userInfo.id + u.id);
      this.$emit("close");
    }
  },
  mounted() {
    $("#searchUserModal").bootstrapMaterialDesign();
    if (this.userInfo.name == "" || !this.userInfo.email == "")
      this.$router.push("/login");
  }
};
</script>

<style scoped>
.user-image {
  height: 40px;
  margin: 5px;
}
.user-list {
  max-height: 500px;
}
.list-group-item:hover {
  background: rgba(0, 0, 0, 0.1);
}
</style>



